body {
  margin: 0;
  font-family: 'Oxygen', serif !important;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Lora", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow-x: hidden; */
}

h1, h2, h3, h4, h5, h6, .navbar {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  text-transform: uppercase;
}
h4, h6 {
  text-transform: none;
}


/* SidePanel */
.navbar-expand-md .navbar-nav .nav-link {
  padding-right: 0rem;
  padding-left: 0rem;
}

.navbar-brand {
  margin-right: 0rem;
}

.panel-header{
  color: #fff;
  align-items: center;
}
.panel-header-expanded h6 {
  color: #e9afb5;
}

.image-cropper {
  width: 11rem;
  height: 11rem;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  position: relative;
  overflow: hidden;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
}
.profile-img {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}
.contact-info {
  font-family: 'Oxygen', serif;
  text-transform: lowercase;
}
.social-links {
  font-size: 1.5rem;
}


/* Introduction */
.carousel {
  /* display:block; */
  max-height: 30rem;
  overflow: hidden;
}
.carousel img {
  position: relative;
  margin: -5% auto;
  width: 100%; /* height will follow within image ratio */
  height: auto; /* to override attribute height set in tag */
  vertical-align: middle; /* vertical centering on baseline */
}
.carousel-caption {
  left: 10%;
  top: 35%;
  font-weight: 500;
  color: #000000;
}

.title {
  font-size: 32px;
  text-transform: none;
  text-shadow: 1px 1px 1px #000;
}

@media screen and (min-width: 530px) {
  .title {
    font-size: 40px;
  }
}
@media screen and (min-width: 700px) {
  .title {
    font-size: 48px;
  }
}

.btn.btn-learn {
  color: #000;
  background: transparent;
  border: 2px solid #000;
  border-radius: 0;
}
.btn.btn-learn:hover {
  box-shadow: 1px 1px 2px #000000;
}

.tinted {
  --left-shade: rgba(0, 0, 0, 1);
  --right-shade: rgba(0, 0, 0, 0.25);
  -webkit-mask-image: linear-gradient(to left, var(--left-shade), var(--right-shade));
  -o-mask-image: linear-gradient(to left, var(--left-shade), var(--right-shade));
  -moz-mask-image: linear-gradient(to left, var(--left-shade), var(--right-shade));
  mask-image: linear-gradient(to left, var(--left-shade), var(--right-shade));
  filter: brightness(110%) saturate(80%);
}

/* Footer */
footer {
  background-color: #333;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

/* Misc */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
